.story {
  padding: 229px 0 176px;
  background-color: #245852;
  color: #fff;

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__content {
    margin-right: 30px;
    max-width: 580px;
  }

  &__heading {
    font-size: 50px;
    line-height: 157%;
    font-weight: 700;
    font-family: 'Georgia', serif;
  }

  &__heading+* {
    margin-top: 33px;
  }

  &__text {
    font-weight: 500;
    font-size: 25px;
    line-height: 1.58;

    &+* {
      margin-top: 57px;
    }
  }

  &__btn {
    display: flex;
  }

  &__video {
    border-radius: 18px;
    overflow: hidden;
    max-width: 895px;
    position: relative;

    &-btn {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: transparent;
    }

    &-overlay {
      position: absolute;
      z-index: 99;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
      transition: opacity 0.2s ease;

      &.hidden {
        opacity: 0;
      }
    }

    &-object {
      max-width: 100%;
      height: auto;
      vertical-align: top;
    }
  }
}

@media (max-width: 1400px) {
  .story__heading {
      font-size: 40px;
    }
  }

  @media (max-width: 1199px) {
    .story {
      padding: 90px 0 90px;

      &__row {
        flex-direction: column-reverse;
      }

      &__video {
        margin-bottom: 40px;
      }

      &__heading {
        line-height: 130%;
      }

      &__text {
        font-size: 20px;
        line-height: 130%;

        &+* {
          margin-top: 30px;
        }
      }
    }
  }

  @media (max-width: 991px) {
    .story {
      padding: 60px 0 60px;

      &__heading {
        font-size: 30px;
        line-height: 130%;
      }

      &__text {
        font-size: 18px;

        &+* {
          margin-top: 20px;
        }
      }
    }
  }

  @media (max-width: 460px) {

    .story {
      &__btn {
        width: 100%;
        display: block;
      }

      &__content {
        margin-right: 0px;
      }
    }
  }
