.header {
  padding-top: 17px;
  padding-bottom: 17px;
  border-bottom: 1px solid #d6d6d6;
  line-height: 1;
  background-color: #fff;
}
.header__logo {
  transition: all 0.3s ease;
}
.header__logo:hover {
  transform: scale(1.2);
}
.header__wrap {
  display: flex;
  align-items: center;
}
.header__nav {
  margin-left: 172px;
  font-weight: 500;
  color: #292929;
}
.header__nav-list {
  display: flex;
}
.header__nav-item + .header__nav-item {
  margin-left: 70px;
}
.header__nav-link {
  transition: color 0.2s ease;
}
.header__nav-link:hover {
  color: #01996d;
}
.header__login {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.header__login > * + * {
  margin-left: 35px;
}
.header__login-link {
  margin-top: -1px;
  color: #000;
  transition: all 0.3s ease;
  position: relative;
}
.header__login-link:hover {
  color: #01996d;
}
.header__login-link::after {
  position: absolute;
  content: " ";
  left: 0;
  bottom: -5px;
  width: 25%;
  background-color: #01996d;
  height: 2px;
  transition: all 0.3s ease;
}
.header__login-link:hover::after, .header__login-link:focus::after {
  width: 100%;
}
.header__burger {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  position: relative;
  z-index: 9999;
  width: 40px;
  height: 30px;
  justify-content: center;
  align-items: center;
  display: none;
}
@media (max-width: 991px) {
  .header__burger {
    display: flex;
  }
}
.header__burger-icon {
  display: block;
  position: relative;
  width: 40px;
  height: 5px;
  background-color: #000;
}
.header__burger-icon::after, .header__burger-icon::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 5px;
  background-color: #000;
  transition: transform 0.15s ease-in, top 0.15s ease-in 0.15s;
}
.header__burger-icon::before {
  left: 0;
  top: -10px;
}
.header__burger-icon::after {
  left: 0;
  top: 10px;
}
.header__burger-icon.active {
  background-color: transparent;
}
.header__burger-icon.active::before {
  transform: rotate(45deg);
  top: 0;
  transition: top 0.15s linear, transform 0.15s ease-in 0.15s;
}
.header__burger-icon.active::after {
  transform: rotate(-45deg);
  top: 0;
  transition: top 0.15s linear, transform 0.15s ease-in 0.15s;
}
.header__btn {
  border: 2px solid #01996d;
  white-space: nowrap;
  transition: all 0.3s ease;
}
.header__btn:hover {
  background: #fff;
  color: #01996d;
}

@media (max-width: 1199px) {
  .header__wrap {
    justify-content: space-between;
