.card__wrap {
  background-color: #fff;
  width: 296px;
  border-radius: 4px;
  box-shadow: 0px 3.62249px 18.1125px rgba(165, 165, 165, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 28px 20px 30px;
  animation: shake 1.5s linear;
  animation-iteration-count: infinite;
}
.card__title {
  position: relative;
  font-weight: 600;
  font-size: 14px;
  line-height: 1;
  padding-bottom: 12px;
  margin-bottom: 13px;
}
.card__title::after {
  content: "";
  position: absolute;
  background-color: #000;
  height: 0.75px;
  left: -7px;
  right: -7px;
  bottom: 0px;
}
.card__avatar-container {
  margin-bottom: 25px;
}
.card__avatar {
  text-align: center;
}
.card__avatar-img {
  margin-bottom: 13px;
  width: 57px;
  height: 57px;
  border-radius: 50%;
  object-fit: cover;
}
.card__avatar-name {
  font-size: 12px;
}
.card__contents {
  text-align: left;
  padding-left: 6px;
}
.card__contents-title {
  font-family: "Georgia", serif;
  font-size: 16px;
  margin-bottom: 22px;
  font-weight: 700;
}
.card__contents-list {
  font-size: 10.62px;
  list-style-type: disc;
  list-style-position: inside;
}
.card__contents-item + .card__contents-item {
  margin-top: 20px;
}

@media (max-width: 500px) {
  .card__wrap {
    max-width: 296px;
  }
}
@keyframes shake {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(2deg);
  }
  30% {
    transform: rotate(0deg);
  }
  40% {
    transform: rotate(-2deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
#preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 999999;
}

#teabag {
  transform-origin: top center;
  transform: rotate(3deg);
  animation: swing 2s infinite;
}

#steamL {
  stroke-dasharray: 13;
  stroke-dashoffset: 13;
  animation: steamLarge 2s infinite;
}

#steamR {
  stroke-dasharray: 9;
  stroke-dashoffset: 9;
  animation: steamSmall 2s infinite;
}

@keyframes swing {
  50% {
    transform: rotate(-3deg);
  }
}
@keyframes steamLarge {
  0% {
    stroke-dashoffset: 13;
    opacity: 0.6;
  }
  100% {
    stroke-dashoffset: 39;
    opacity: 0;
  }
}
@keyframes steamSmall {
  10% {
    stroke-dashoffset: 9;
    opacity: 0.6;
  }
  80% {
    stroke-dashoffset: 27;
    opacity: 0;
  }
  100% {
    stroke-dashoffset: 27;
    opacity: 0;
  }
}